function elementUtilities(cy) {
  return {
    moveNodes: function moveNodes(positionDiff, nodes, notCalcTopMostNodes) {
      var topMostNodes = notCalcTopMostNodes ? nodes : this.getTopMostNodes(nodes);
      topMostNodes.positions(function (ele, i) {
        return {
          x: topMostNodes[i].position("x") + positionDiff.x,
          y: topMostNodes[i].position("y") + positionDiff.y
        };
      });

      for (var i = 0; i < topMostNodes.length; i++) {
        var node = topMostNodes[i];
        var children = node.children();
        this.moveNodes(positionDiff, children, true);
      }
    },
    getTopMostNodes: function getTopMostNodes(nodes) {
      //*//
      var nodesMap = {};

      for (var i = 0; i < nodes.length; i++) {
        nodesMap[nodes[i].id()] = true;
      }

      var roots = nodes.filter(function (ele, i) {
        if (typeof ele === "number") {
          ele = i;
        }

        var parent = ele.parent()[0];

        while (parent != null) {
          if (nodesMap[parent.id()]) {
            return false;
          }

          parent = parent.parent()[0];
        }

        return true;
      });
      return roots;
    },
    rearrange: function rearrange(layoutBy) {
      if (typeof layoutBy === "function") {
        layoutBy();
      } else if (layoutBy != null) {
        var layout = cy.layout(layoutBy);

        if (layout && layout.run) {
          layout.run();
        }
      }
    },
    convertToRenderedPosition: function convertToRenderedPosition(modelPosition) {
      var pan = cy.pan();
      var zoom = cy.zoom();
      var x = modelPosition.x * zoom + pan.x;
      var y = modelPosition.y * zoom + pan.y;
      return {
        x: x,
        y: y
      };
    }
  };
}

module.exports = elementUtilities;